
.flex-container {
    display: flex;
    flex-wrap: wrap;
    align-content: space-around;
    flex-flow: wrap;
    justify-content: space-around;
    margin: 50px auto 0px auto;
    padding-bottom: 30px;
    font-size: 50px;
    text-align: center;
    width: auto;
    align-items: center;
    justify-content: center;
}
.domainRing {
    justify-content: flex-end;
    margin-top: 25px;
    margin-bottom: 25px;
    width: auto;
    color: white;
    flex: 25% 1;
  }
  .outerring {
    margin: auto;
    background: linear-gradient( 360deg, #0b9e19 15.45%, rgba(5, 89, 13, 0.57) 48.72%, rgba(0, 30, 5, 0) 87.53% );
    border-radius: 50%;
    z-index: 0;
    height: 250px;
    width: 250px;
    display: flex;
  }
  .innerRing {
    position: relative;
    color: white;
    background-color: #061509;
    z-index: 1;
    top: 10px;
    left: 10px;
    border-radius: 50%;
    height: 230px;
    width: 230px;
    display: flex;
    position: relative;
    align-items: center;
    justify-content: center;
  }
.innerRing .flip-card-back {
    height: 100%;
    width: 75%;
    display: flex;
    justify-content: center;
    align-items: center;
    position: absolute;
    word-break: break-word;
    text-align: center;
  }
  .innerRing .flip-card-front {
    height: 100%;
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    position: absolute;
  }
  .span{
    font-size: 20px;
  }
  .ringImage {
    height: 200px;
    background-color: transparent;
    position: absolute;
    top:-1%;
    z-index: 0;
  }
  .innerRing .flip-card-front p {
    bottom: 10%;
    font-size: 25px;
    background-color: transparent;
    font-weight: 600;
    text-align: center;
    position: absolute;
  }
  .domainPage {
    height: 100%;
    background: rgb(5, 79, 17);
    background: linear-gradient( 135deg, rgba(5, 79, 17, 0.99) 9%, rgba(0, 0, 0, 1) 50%, rgba(5, 62, 15, 1) 93% );
    box-shadow: 0px 4px 60px -1px rgba(0, 0, 0, 0.2), inset 0px 4px 50px rgba(0, 0, 0, 0.25);
    filter: drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.25));
  }
  .domainScreen {
    background-image: url('../../assets/domain/Background.webp');
    background-repeat: no-repeat;
    background-size: cover;
    height: 90vh;
    overflow: hidden;
    width: 100%;
  }
  .DomainScroll {
    height: 100%;
    /* overflow-y: scroll; */
    width: 100%;
    display: flex;
    justify-content: space-evenly;
    flex-direction: column;
    align-items: center;
    position: relative;
  }

  .domainSlider {
    top: 40px;
    display: block;
    margin: 0px auto;
    width: 90%;
    position: absolute;
    overflow: hidden;
    height: 70%;
    display: block;
  }
  .domainheading {
    width: 30%;
    margin: 45px auto 0 auto;
    bottom: 20px;
    height: 15%;
    position: absolute;
    text-align: center;
  }
  .domainheading h2 {
    color: #74eb76;
    font-weight: 400;
    font-size: 4.5vw;
    line-height: 15px;
    height: 20px;
    font-family: "Monument Extended", sans-serif;
  }
  .domainheading p {
    margin-top: 30px;
    height: 40px;
    line-height: 20px;
    font-feature-settings: 'liga' off;
    color: white;
  }

  .flip-card-back{
    opacity: 0;
}
.all-dots {
    visibility: hidden;
  }
.outerring:hover {
    transform: rotateY(180deg) scaleX(-1);
    position: static;
    /* backface-visibility: hidden; */
    text-align: center;
    transition: 2s;
  }

  .outerring:hover .flip-card-front {
    transition: 0.6s;
    visibility: hidden;
  }
  .outerring:hover .flip-card-back {
    transition: opacity 6s; /* specify the property you want to animate */
    opacity: 1; /* specify the final state */
    
  }
   .innerRing:hover .flip-card-front {
    transition: 0.6s;
    visibility: hidden;
  }
  .innerRing:hover .flip-card-back {
    transition: opacity 6s; /* specify the property you want to animate */
    opacity: 1; /* specify the final state */
    
  }
  @media only screen and (max-width: 1100px){
    .domainheading {
        width: 40%;
      }
      .domainScreen {
        height: 67vh;
      }
      .domainheading {
        width: 50%;
        margin-top: 15px;
      }
      
  .domainSlider {
    height: 50%;
  }

  }
  @media only screen and (max-width: 790px){
  .domainSlider {
    height: 45vh;
    width: 95%;
  }
  .domainScreen {
    height: 75vh;
  }
  .arrows {
    visibility: hidden;
}
.all-dots {

    visibility: visible;
  }
  .domainheading {
    width: 65%;
    margin-bottom: 30px;
  }
  .domainheading h2 {
    font-size: 55px;
  }

  }

  @media only screen and (max-width: 500px){
    .domainScreen {
      height: 95vh;
    }
    .domainheading {
      width: 70%;
      margin-bottom: 60px;
    }
    
  
    }
@keyframes domainAnimation {
  0% {
    transform: rotateY(0deg) scaleX(-1);
  }
  50% {
    transform: rotateY(180deg) scaleX(-1);
  }
  100%{
    transform: rotateY(0deg) scaleX(-1);
  }
}