* {
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

.footer-items{
    z-index:100;
    position: fixed;
    left: 0;
    bottom: 0;
    width: 100%;
    color: white;
    text-align: center;
    border-top: 0.5px solid rgb(83, 80, 80);
    border-left: 0.5px solid rgb(83, 80, 80);
    border-right: 0.5px solid rgb(83, 80, 80);
    border-top-left-radius: 45px;
    border-top-right-radius: 45px;
    margin-top: 1em;
    backdrop-filter: blur(6px);
    background-color: #ffffff11;

}

