
navbar{
    position: sticky;
    z-index:100;
    top: 0px;
}

.header{
    position: relative;
    width: 100%;
    height: 4em;
    display: flex;
    justify-content: space-between;
    align-items: center;
    background: #000;
    padding: 0 25px;
    transform: 0.3s;
}

.logo a{
    display: block;
}

.logo a img{
    display: block;
    width: 15rem;
}

.navigation{
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.navbar-items{
    display: flex;
    justify-content: center;
    flex-direction: row;
    align-items: center;
    z-index: 1;
    transition: 0.5s;
}
.dropdown > li:first-child {
    margin: 1.5em 0 0;
}

.navbar-items li{
    list-style-type: none;
}

.navbar-items li a{
    color: white;
    text-decoration: none;
    display: block;
    padding: 15px 25px;
    font-size: 18px;
    line-height: 1;
    transition: 0.3s;
}

.navbar-items li:after {
    display:block;
    content: '';
    border-bottom: solid 0.1em #68fe04; 
    transform: scaleX(0);  
    transition: transform 250ms ease-in-out;
  }

.navbar-items li:hover:after { 
    margin-top: 0em;
    transform: scaleX(2); 
    transform: scaleY(2);
}
.navbar-items li:after{  
    margin-top: 0em;
    transform-origin:  0% 0%; 
}

.signInButton:hover{
    cursor: pointer;
    /* margin-bottom: 5px; */
    color:black;
}

.hamburger{
    position: relative;
    width: 30px;
    height: 4px;
    background: white;
    border-radius: 10px;
    cursor: pointer;
    z-index: 2;
    transition: 0.3s;
}

.hamburger:before, .hamburger:after{
    content: "";
    position: absolute;
    height:4px;
    right: 0;
    background: white;
    border-radius: 10px;
    transition: 0.3s;
}

.hamburger:before{
    top: -10px;
    width: 20px;
}

.hamburger:after{
    top: 10px;
    width: 25px;
}

.toggle-menu{
    position: absolute;
    width:30px;
    height: 100%;
    z-index: 3;
    cursor: pointer;
    opacity:0;
}

.signInButton{
    color: white;
    text-decoration: none;
    font-size: 1.25em;
    font-family:'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    border-radius: 4em;
    padding-bottom: 0.3em;
    padding-right: 0.7em;
    padding-left: 0.7em;
    text-align: center;
    justify-content: center;
    background-image: linear-gradient(to right, rgb(9, 64, 0), rgb(13, 220, 9));
}

.hamburger, .toggle-menu{
    display: none;
}

.navigation input:checked ~ .hamburger{
    background: transparent;
}

.navigation input:checked ~ .hamburger:before{
    top: 0;
    transform: rotate(-45deg);
    width: 30px;
}

.navigation input:checked ~ .hamburger:after{
    top: 0;
    transform: rotate(45deg);
    width: 30px;
}

.navigation input:checked ~ .navbar-items{
    right:0;
}


/* Responsive */
@media screen and (max-width:992px){
    .hamburger, .toggle-menu{
        display: block;
    }
    .header{
        padding: 5px 5px;
    }
    .navbar-items{
        justify-content: start;
        flex-direction: column;
        align-items: center;
        position: fixed;
        top: 0;
        right: -300px;
        background: #11220D;
        width: 250px;
        height: 100%;
        padding-top: 30px;
    }
    .navbar-items li{
        width: 100%;
        height: 70px;
    }
    .navbar-items li a{
        padding: 25px;
        font-size: 15px;
    }

    .signInButton{
        width: 100%;
    }

    .dropdown{
        width: 100%;
    }
    .logo a img{
        width: 10rem;
    }
}


/* Arrow associated with domain */
.arrow {
    border: solid white;
    border-width: 0 3px 3px 0;
    display: inline-block;
    margin: 0.3em;
    padding: 0.15em;
  }

.down {
    transform: rotate(45deg);
    -webkit-transform: rotate(45deg);
  }


  /* Dropdown */
.domains {
    position: relative;
}
.aboutUs {
    position: relative;
}

.dropdown {
    background-color: #11220D;
    margin-top: 0em;
    position: absolute;
    /*WITH RESPECT TO PARENT*/
    display: none;
    border-radius: 0.3em;
}

.dropdown li {
    margin: 2px;
    width: 9em;
    height: 3em;
    text-align: center;
}

.dropdown li a{
    padding: 0;
    margin: 7px;
    text-align: center;
}

.domains:hover .dropdown {
    display: block;
}

.aboutUs:hover .dropdown {
    display: block;
}

.dropdown li a:hover{
    padding: 0;
    margin: 7px;
    text-align: center;
    /* font-size: 20px; */
}
